.company-grid-item {
  padding: 15px;
  position: relative;
  @include media-breakpoint-down(xl) {
    padding: 10px;
  }
  @include media-breakpoint-down(md) {
    padding: 5px;
  }

  .add-to-favourite {
    position: absolute;
    right: 30px;
    bottom: 47px;
    width: 18px;
    height: 18px;
    z-index: 2;
    color: $color-gray-1;
    @include media-breakpoint-down(xl) {
      right: 25px;
      bottom: 33px;
    }
    @include media-breakpoint-down(md) {
      right: 19px;
      bottom: 27px;
    }

    svg {
      width: 100%;
      height: 100%;
      max-width: 100%;
      max-height: 100%;
    }

    &:hover {
      color: $color-orange-2;
    }

    &.active {
      color: $color-orange-2;

      svg {
        * {
          fill: $color-orange-2;
        }
      }
    }
  }

  .box {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    display: block;
    @include media-breakpoint-down(xl) {
      border-radius: 7px;
    }

    .img {
      padding-bottom: 67%;
      position: relative;
      overflow: hidden;
      display: block;
      @include media-breakpoint-down(xl) {
        padding-bottom: 68%;
      }

      img.bg-img {
        transform: scale(1);
        transition: .6s;
      }

      img.company-logo {
        position: absolute;
        z-index: 1;
        left: 10px;
        top: 10px;
        width: auto;
        height: auto;
        display: block;
        max-width: 80px;
        max-height: 80px;
        @include media-breakpoint-down(xl) {
          max-width: 76px;
          max-height: 76px;
        }
        @include media-breakpoint-down(md) {
          max-width: 72px;
          max-height: 72px;
        }
      }

      .date {
        position: absolute;
        z-index: 2;
        color: #fff;
        font-family: $barista-font;
        font-size: 26px;
        font-style: normal;
        font-weight: 400;
        line-height: 100%;
        bottom: 10px;
        right: 12px;
      }

      & + .text {
        padding-top: 23px;
        padding-bottom: 26px;
        @include media-breakpoint-down(xl) {
          padding-top: 16px;
          padding-bottom: 16px;
        }

        h2.company-title {
          font-size: 24px;
          @include media-breakpoint-down(xl) {
            font-size: 20px;
          }
        }
      }
    }

    .text {
      background: #fff;
      padding: 30px 16px;
      display: block;
      @include media-breakpoint-down(xl) {
        padding: 20px 16px;
      }

      h2.company-title {
        margin-bottom: 0;
        font-size: 20px;
        font-style: normal;
        font-weight: 300;
        line-height: 120%;
        color: #000;
        letter-spacing: -0.015em;
        font-family: $roboto-condensed-font;
        @include media-breakpoint-down(xl) {
          font-size: 20px;
        }
      }

      .category-location {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
        margin-top: 14px;
        @include media-breakpoint-down(xl) {
          margin-top: 20px;
        }
        @include media-breakpoint-down(md) {
          margin-top: 11px;
        }

        .category {
          color: $color-green-1;
          font-family: $roboto-condensed-font;
          font-size: 13px;
          font-style: normal;
          font-weight: 500;
          line-height: 160%;
          text-transform: uppercase;
          margin-right: 22px;
        }

        .location {
          color: rgba(0, 0, 0, 0.75);
          font-family: $roboto-condensed-font;
          font-size: 12px;
          font-style: normal;
          font-weight: 300;
          line-height: 160%;
          position: relative;
          padding-left: 17px;
          margin-right: 12px;

          &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 0;
            width: 9px;
            height: 12px;
            transform: translateY(-50%);
            background: url('../../../images/icons/location-filled.svg') no-repeat 50% 50%/ contain;
          }

        }
      }
    }

    &.-adv {
      background: $color-blue-1;
      min-height: 470px;
      z-index: 0;
      padding: 293px 28px 46px;

      &:after {
        content: '';
        position: absolute;
        right: 54px;
        top: 60%;
        width: 46px;
        height: 60px;
        z-index: -1;
        background: url('../../../images/icons/dots.svg') no-repeat 50% 50%/ contain;
      }

      .img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -2;
        padding-bottom: 0;
      }

      .text {
        padding: 0;
        background: transparent;
        width: 100%;
      }

      .adv-logo {
        width: 80px;
        height: 41px;
        margin-right: 24px;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }

      h2.adv-title {
        color: #fff;
        font-family: $barista-font;
        font-size: 64px;
        font-style: normal;
        font-weight: 400;
        line-height: 80%;
        @include media-breakpoint-down(md) {
          font-size: 48px;
        }
      }

      .adv-subtitle {
        color: rgba(255, 255, 255, 0.80);
        font-family: $roboto-condensed-font;
        font-size: 20px;
        font-style: normal;
        font-weight: 300;
        line-height: 160%;
        margin-top: 33px;
        letter-spacing: -0.009em;
        @include media-breakpoint-down(md) {
          font-size: 16px;
        }
      }

      .adv-highlight {
        color: $color-yellow-3;
        font-family: $roboto-condensed-font;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 120%;
        margin-top: 38px;
        letter-spacing: -0.009em;
        @include media-breakpoint-down(md) {
          font-size: 16px;
        }
      }
    }

    &.-video {
      cursor: pointer;

      .img {
        padding-bottom: 140%;

        &:before {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 100px;
          background: linear-gradient(0deg, rgba(50, 50, 50, 0.80) 0.52%, rgba(0, 0, 0, 0) 100%);
          z-index: 1;
        }

        &:after {
          content: '';
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 100px;
          height: 100px;
          background: url('../../../images/icons/play.svg') no-repeat 50% 50%/contain;
          opacity: .5;
          transition: .4s;
        }
      }

      .text {
        padding: 16px 22px;

        h2.company-title {
          color: rgba(0, 0, 0, 0.75);
          font-family: $roboto-condensed-font;
          font-size: 20px;
          font-style: normal;
          font-weight: 300;
          line-height: 120%;
        }
      }

    }

    &:not(.-video) {
      &:hover {
        .img {
          img.bg-img {
            transform: scale(1.04);
          }
        }
      }
    }

    &.-video {
      &:hover {
        .img {
          img.bg-img {
            transform: scale(1.04);
          }

          &:after {
            opacity: .75;
          }
        }
      }
    }
  }
}